import React, { useState } from 'react';
import { Form, Input, Button } from 'antd';
import Cookies from 'js-cookie';
import { changePasswordAPI } from 'services/auth/user';

const { Item, useForm } = Form;

const ChangePasswordForm = props => {
  const [isLoading, setIsLoading] = useState(false);
  const [form] = useForm();
  const accessToken = Cookies.get('access');


  const handleChangePassword = async values => {
    setIsLoading(true);
    try {
      await changePasswordAPI(accessToken, values);
      form.resetFields();
    } catch (error) {
      console.log(error);
    }
    setIsLoading(false);
  }

  return (
    <Form
      form={form}
      style={{maxWidth: 600}}
      layout="vertical"
      onFinish={handleChangePassword}>
      <div><strong>i mt khu</strong></div>
      <Item label="Mt khu c" name="old_password" required>
        <Input.Password />
      </Item>
      <Item label="Mt khu mi" name="new_password" required>
        <Input.Password />
      </Item>
      <Item label="Nhp li mt khu mi" name="confirm_password" required>
        <Input.Password />
      </Item>
      <Item>
        <Button loading={isLoading} disabled={isLoading} htmlType="submit">i mt khu</Button>
      </Item>
    </Form>
  )
}

export default ChangePasswordForm;